.archive {
  font-size: 1.125rem;
}

.archive__wrapper {
  flex-direction: column;
  display: flex;
}

.archive__wrapper a {
  color: #fff;
  text-decoration: none;
}

.archive__wrapper a :hover {
  color: #ffffff80;
}

.archive__grid {
  border-bottom: 1px solid #ffffff1a;
  grid-template-rows: auto;
  grid-template-columns: .4fr .8fr 2fr .1fr;
  padding: 1rem 0 .5rem;
  display: grid;
}

@media (width <= 640px) {
  .archive__grid {
    grid-template-rows: auto;
    grid-template-columns: 1fr .1fr;
    row-gap: .5rem;
    line-height: 110%;
  }

  .archive__grid-item:first-child, .archive__grid-item:nth-child(2), .archive__grid-item:nth-child(3) {
    grid-column: 1 / span 1;
  }

  .archive__grid-item:nth-child(4) {
    grid-area: 1 / 2 / span 1 / span 1;
    justify-content: flex-end;
    align-items: flex-start;
    display: flex;
  }
}

.header__subpage {
  margin-top: 100px;
}

.about__content {
  flex-direction: column;
  gap: 40px;
  padding: 80px 0;
  display: flex;
}

.about__wrapper {
  flex-direction: column;
  gap: 24px;
  display: flex;
}

.about p {
  font-size: 20px;
  line-height: 120%;
}

.about p span {
  color: #fff3;
  font-weight: 500;
  display: block;
}

.about__title-color {
  color: #ffb800;
  font-weight: 500;
}

@media (width <= 450px) {
  .about p {
    font-size: 1rem;
  }
}

